* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
}
img {
  position: relative;
  height: 20%;
  width: 110%;
  margin-bottom: 25px;
}
.root {
  position: absolute;
  top: 137px;
  left: 71px;
  color: #fff;
  font-weight: bold;
  font-size: 12px;
}
.sidenav {
  height: 100%;
  width: 230px;
  position: fixed;
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  background-color: #1f2229;
  overflow-x: scroll;
  padding-top: 20px;
}

.sidenav a {
  padding: 15px;
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
  color: #7384b0;
}

.sidenav a:hover {
  color: white;
  background-color: #40434a;
}

.main-block {
  margin-left: 210px;
  padding: 0px 10px;
  background-color: #1f2229;
  background-size: cover;
}
#main-block1{
  display: none;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 12px;
  }
}
a {
  display: flex;
  text-decoration: none;
  font-size: 12px;
  color: black;
}

ion-icon {
  font-size: 12px;
  margin-right: 13px;
  margin-left: 13px;
}
.space {
  display: flex;
  margin-left: 30px;
  margin-top: 50px;
  color: #7384b0;
}
.line {
  border-bottom: 2px solid white;
  margin-bottom: 30px;
}
.navbar-line {
  border-bottom: 2px solid #4f5967;
  margin-bottom: 10px;
}
.setting-icon {
  margin-left: 80px;
}

/* search machines */

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.top-bar {
  background-color: #1f2229;
  color: #555;
  padding-top: 5px;
  padding-left: 7px;
  padding-bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-box {
  padding-top: 8px;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.search-box input[type="text"] {
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 16px;
  outline: none;
  width: 300px;
}
.icon-reorder {
  height: 23px;
  width: 23px;
  color: #add8e6;
}
.icon-reorder:hover {
  transition: all 0.3s;
  color: #fff;
  cursor: pointer;
}
.icon-search {
  height: 20px;
  width: 20px;
}

.icon-button {
  display: inline-block;
  background-color: #2e353f;
  border: none;
  padding-top: 2px;
  padding-bottom: 2px;
  margin-left: 15px;
}
.icon-button-pin {
  display: inline-block;
  background-color: #1f2229;
  border: none;
  padding-top: 2px;
  padding-bottom: 2px;
  color: #fff;
}
.icon-pin {
  height: 20px;
  width: 20px;
}
.icon-pin:hover {
  cursor: pointer;
}
.header h2,
p {
  font-size: 16px;
  color: #fff;
  font-weight: 100;
}
.header .spanspecial {
  color: #add8e6;
  font-weight: bold;
}
/* dashboard */
.dashboard {
  margin-left: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cards {
  display: grid;
  grid-template-columns: auto auto auto;
  margin-left: 24px;
}
.library-grid {
  display: grid;
  grid-template-columns: 3fr 1fr;
  column-gap: 10px;
  margin-left: 24px;
}
.forchange {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.Technical-gap {
  margin-left: 20px;
}
a {
  /* margin-left: 15px; */
  font-size: 14px;
  color: #a7b1c2;
  font-weight: bold;
}
.card {
  background: #2e353f;
  padding: 20px;
  width: 320px;
  height: 120px;
  text-align: center;
}
.library-card {
  background: #2e353f;
  padding: 20px;
  margin-bottom: 20px;
  margin-top: 20px;
}
.healthy h3 {
  color: limegreen;
}

.warning h3 {
  color: red;
}

.green span {
  color: limegreen;
}

.chart-container {
  margin-top: 20px;
  background: #2e353f;
  padding: 20px;
  width: 670px;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto auto auto;
  gap: 30px;
  padding: 10px;
  background-color: #1f2229;
  color: white;
  font-family: sans-serif;
  margin-left: 10px;
}
.grid-container-2 {
  display: grid;
  grid-template-columns: 3fr 5fr;
  padding: 10px;
  background-color: #1f2229;
  color: white;
  font-family: sans-serif;
  /* margin-left: 10px; */
}
.grid-item {
  background-color: #2e353f;
  border-top: 3px solid #4f5967;
  /* padding: 20px; */
}
.grid-item-2 {
  background-color: #2e353f;
  padding: 18px;
}
.no-of-cups-realtime {
  position: relative;
}
/* Specific item styling based on content */

.machine-health {
