}
@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr;
  }

  .machine-health,
  .time-since-cleaned,
  .coffee-amount,
  .live-chart,
  .no-of-cups-realtime,
  .total-cups {
    grid-column: 1 / 2;
  }
}
.machine-health,
.time-since-cleaned,
.coffee-amount,
.no-of-cups-realtime,
.total-cups {
  height: 120px;
}

